import * as React from 'react';
import { styled } from '@mui/material/styles';
import Grid from '@mui/material/Grid';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import Link from '@mui/material/Link';
// import { createTheme, ThemeProvider, styled } from '@mui/material/styles';

const Item = styled(Paper)(({ theme }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
  ...theme.typography.body2,
  padding: theme.spacing(1),
  textAlign: 'center',
  color: theme.palette.text.secondary,
  boxShadow: 'none',
}));

export default function RowAndColumnSpacing() {
  return (
    <Box sx={{ margin: '0px 50px'}}>
      <Divider></Divider>
      <Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 2, md: 3 }}>
        <Grid item xs={6} md={2}>
          <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
        </Grid>
        <Grid item xs={6} md={2}>
          <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
        </Grid>
        <Grid item xs={6} md={2}>
        <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
          <Item><Link href="#" color="inherit" underline="hover"></Link></Item>
        </Grid>
        <Grid item xs={6} md={6}>
          <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item>XX-XXXX-XXXX</Item>
          <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item>gdfuntea@gmail.com</Item>
          <Item sx={{ color: "black", fontWeight: 'bold' }}></Item>
          <Item>XXXXXXXX</Item>
        </Grid>
      </Grid>
      
      <Typography sx={{ fontSize: { xs: "10px", md: "15px" } }} gutterBottom component="div" marginTop="25px" align="center">
       2022 Good Fun Tea, All Rights Reserved
      </Typography>
    </Box>
    
  );
}
